import axios from 'axios';
import $ from 'jquery';
import router from '../router';
import store from '.';


export default{
  state: {
    products: [],
    product: {},
    //pagination: {},
    category: [],
    searchText: ''
  },
  actions: {
    getProducts(context) {
      //const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products?page=${page}`;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products/all`;
      context.commit('LOADING', true);
      context.commit('SEARCHTEXT', '');
      axios.get(url).then((response) => {
        if(response.data.success){
          context.commit('PRODUCTS', response.data.products);
          store.dispatch('getCategory');
          //context.commit('PAGINATION', response.data.pagination);
        }else{
          const message= response.data.message;
          const status= 'danger';
          store.dispatch('updateMessage', {message, status});
        };
        context.commit('LOADING', false);
        //console.log(context.state.products);
      });
    },
    getProduct(context, id) {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
      let item= 'product';
      let status= id;
      //context.commit('LOADINGITEM', {item, status});
      context.commit('LOADING', true);
      axios.get(url).then((response) => {
        if(response.data.success){
          context.commit('PRODUCT', response.data.product);
        }else{
          const message= response.data.message;
          const status= 'danger';
          store.dispatch('updateMessage', {message, status});
        };
        context.commit('LOADING', false);
        //context.commit('LOADINGITEM', {item});
        router.push(`/Product/${id}`);
      });
    },
    addToCart(context, {id, qty}) {
      const url= `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      context.commit('LOADINGITEM', id);
      //console.log(id, qty);
      const cart= {
        product_id: id,
        qty,
      };
      axios.post(url, { data: cart }).then((response) => {
        store.dispatch('getCart');
        const message= response.data.message;
        const status= 'danger';
        store.dispatch('updateMessage', {message, status});
        context.commit('LOADINGITEM', '');
        //$('#productModal').modal('hide');
        //console.log(response.data);
      });
    },
    getCategory(context) {
      const categories = new Set();
      context.state.products.forEach((item) => {
        categories.add(item.category);
      });
      context.commit('CATEGORY', Array.from(categories));
      //console.log(context.state.category);
    },
    changeSearchText(context, value) {
      context.commit('SEARCHTEXT', value);
    }
  },
  mutations: {
    PRODUCTS(state, payload){
      state.products= payload;
    },
    PRODUCT(state, payload){
      state.product= payload;
    },
    // PAGINATION(state, payload){
    //   state.pagination= payload;
    // },
    CATEGORY(state, payload){
      state.category= payload;
    },
    SEARCHTEXT(state, value){
      state.searchText= value;
    }
  },
  getters: {
    products(state){
      return state.products;
    },
    product(state){
      return state.product;
    },
    // pagination(state){
    //   return state.pagination;
    // },
    category(state){
      return state.category;
    },
    searchText(state){
      return state.searchText;
    }
  }
}


