<template>
  <div>
    <h5><b class="text-primary">訂單資料</b></h5>
    <br>
    <form class="col-md-12 px-1" @submit.prevent="createOrder">
      <div class="row">
        <div class="form-group col-6">
          <label for="username">收件人姓名</label>
          <input type="text" class="form-control" name="name" id="username" v-model="form.user.name" v-validate="'required'" placeholder="輸入姓名">
          <span class="text-danger" v-if="errors.has('name')">姓名欄位不能留空</span>
        </div>
        <div class="form-group col-6">
          <label for="usertel">收件人電話</label>
          <input type="tel" class="form-control" id="usertel" v-model="form.user.tel" placeholder="請輸入電話">
        </div>
      </div>
      <div class="form-group">
        <label for="useremail">Email</label>
        <input type="email" class="form-control" name="email" id="useremail" v-model="form.user.email" v-validate="'required|email'" placeholder="請輸入 Email" required>
        <span class="text-danger" v-if="errors.has('email')">{{errors.first('email')}}</span>
      </div>
      <div class="form-group">
        <label for="useraddress">收件人地址</label>
        <input type="text" class="form-control" name="address" id="useraddress" v-model="form.user.address"
          placeholder="請輸入地址">
        <span class="text-danger" v-if="errors.has('address')">地址欄位不能留空</span>
      </div>
      <div class="form-group">
        <label for="comment">留言</label>
        <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
      </div>
      <div class="text-right">
        <button class="btn btn-outline-primary">
          <i class="fas fa-spinner fa-spin"  v-if="loadingItem== 'submit'"></i> 送出訂單
        </button>
      </div>
    </form>
  </div>
</template>
<script>
import {mapGetters, mapActions} from 'vuex';
import $ from 'jquery';

export default {
  data() {
    return {
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: ''
        },
        message: ''
      }
    };
  },
  computed: {
    ...mapGetters(['loadingItem'])
  },
  methods: {
    createOrder() {
      const vm = this;
      vm.$store.dispatch('updateLoadingItem', 'submit');
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
      const order = vm.form;
      this.$validator.validate().then((result) => {
        if (result) {
          this.$http.post(url, { data: order }).then((response) => {
            //console.log('訂單已建立', response);
            if (response.data.success) {
              vm.$router.push(`/Checkout/${response.data.orderId}`);
            }
            vm.$store.dispatch('updateLoadingItem', false);
            const message= response.data.message;
            const status= 'primary';
            vm.$store.dispatch('updateMessage', {message, status});
          });
        } else {
          const message= response.data.message;
          const status= 'danger';
          vm.$store.dispatch('updateMessage', {message, status});
        }
      });
    }
  },
  created() {

  },
};
</script>


