<template>
  <div>
    <div class="card border-0" v-if="cardData">
      <div class="card-img" :style="{backgroundImage: `url(${cardData.imageUrl})`}" v-if="cardData.is_enabled=== 1">
        <button class="card-img-text btn btn-outline-light" @click="getProduct(cardData.id)">查看商品</button>
      </div>
      <div class="card-img-disable" :style="{backgroundImage: `url(${cardData.imageUrl})`}" v-if="cardData.is_enabled!== 1">
      </div>
      <h5 class="text-center text-light bg-danger w-100" style="position: absolute; top: 60px;" 
        v-if="cardData.is_enabled!== 1">目前缺貨中</h5>
      <div class="card-body">
        <h5 class="card-title row d-flex justify-content-between">
          <strong class="text-primary">{{ cardData.title }}</strong>
          <span class="badge badge-secondary float-right" @click.prevent="changeSearchText(cardData.category)">
            <small>{{ cardData.category }}</small>
          </span>
        </h5>
        <p class="text-muted">{{ cardData.description }}</p>
        <div class="row d-flex justify-content-between">
          <div class="h5 w-100" v-if="cardData.origin_price=== cardData.price">NT${{ cardData.origin_price }} </div>
          <del class="h7 text-muted w-100" v-if="cardData.origin_price!== cardData.price">NT${{ cardData.origin_price }} </del>
          <div class="h5 strong text-secondary" v-if="cardData.origin_price!== cardData.price"> NT${{ cardData.price }} </div>
          <button type="button" class="btn btn-outline-danger btn-sm ml-auto" v-if="cardData.is_enabled=== 1" @click="addToCart(cardData.id)">
            <i class="fas fa-spinner fa-spin" v-if="loadingItem=== cardData.id"></i><i class="fas fa-shopping-cart fa-1.5x" v-else></i>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {mapGetters, mapActions} from 'vuex';
import $ from 'jquery';

export default {
  props: ['cardData'],
  computed: {
    ...mapGetters(['loadingItem', 'searchText']),
    filterData() {
      const vm= this;
      const data= vm.products;
      if (vm.searchText) {
        return data.filter((item, index, array) => {
          return item.category=== vm.searchText; 
        });
      }else{
        return data;
      }
    },
  },
  methods: {
    getProduct(id) {
      this.$store.dispatch('getProduct', id);
    },
    addToCart(id, qty= 1) {
      this.$store.dispatch('addToCart', {id, qty});
    },
    changeSearchText(value){
      this.$store.dispatch('changeSearchText', value);
    },
  },
  created() {

  },
};
</script>


