<template>
  <div>
    <div :id="carouselId" class="carousel slide" data-ride="carousel">
      <!--<ol class="carousel-indicators">
        <li :data-target="carouseHref" data-slide-to="0" class="active bg-light"></li>
        <li :data-target="carouseHref" data-slide-to="1" class="bg-light"></li>
        <li :data-target="carouseHref" data-slide-to="2" class="bg-light"></li>
      </ol>-->
      <div class="carousel-inner">
        <div class="carousel-item active">
          <slot name="item1"></slot>
        </div>
        <div class="carousel-item">
          <slot name="item2"></slot>
        </div>
        <div class="carousel-item">
          <slot name="item3"></slot>
        </div>
      </div>
      <a class="carousel-control-prev bg-dark" :href="carouseHref" role="button" data-slide="prev">
        <span class="carousel-control-prev-icon" aria-hidden="true"></span>
        <span class="sr-only">Previous</span>
      </a>
      <a class="carousel-control-next bg-dark" :href="carouseHref" role="button" data-slide="next">
        <span class="carousel-control-next-icon" aria-hidden="true"></span>
        <span class="sr-only">Next</span>
      </a>
    </div>
  </div>
</template>
<script>
export default {
  props:['carouselId', 'carouseHref']
}
</script>


