<template>
  <div>
    <div class="row">
      <div class="justify-content-between col-12 col-md-9 col-xl-6 py-3">
        <img class="img-fluid" src="https://images.unsplash.com/photo-1504635897574-8e2d499d981f?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=60" height="300px" alt="">
      </div>
      <div class="justify-content-between col-12 col-md-9 col-xl-6 py-3 px-3">
        <h5><b class="text-primary">訂單查詢</b></h5>
        <br>
        <div class="input-group input-group-sm py-2">
          <input type="text" class="form-control border-primary" placeholder="請輸入訂單編號" v-model="searchId">
          <div class="input-group-append">
            <button class="btn btn-outline-primary" type="button" @click.prevent="searchOrder">
              <i class="fas fa-spinner fa-spin" v-if="loadingItem"></i> 送出
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {mapGetters, mapActions} from 'vuex';
import $ from 'jquery';

export default {
  components: {
    
  },
  data() {
    return {
      searchId: ''
    }
  },
  computed: {
    ...mapGetters(['loadingItem']),
  },
  methods: {
    searchOrder(){
      //this.$router.push(`/Checkout/${this.searchId}`);
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order/${vm.searchId}`;
      vm.$store.dispatch('updateLoadingItem', true);
      this.$http.get(url).then((response) => {
        if(response.data.order){
          vm.$router.push(`/Checkout/${this.searchId}`);
        }else{
          const message= '查無此訂單';
          const status= 'danger';
          vm.$store.dispatch('updateMessage', {message, status});
        }
        vm.$store.dispatch('updateLoadingItem', false);
      });
    }
  },
  created() {

  },
};
</script>


