<template>
  <div>
    <div class="text-right">
      <button class="btn btn-secondary" @click="openCouponModal(true)">
        建立新的優惠券
      </button>
    </div>
    <div class="col-12 my-5 overflow-auto">
      <table class="table">
        <thead>
          <tr>
            <th>名稱</th>
            <th>優惠碼</th>
            <th>折扣百分比</th>
            <th>到期日</th>
            <th>是否啟用</th>
            <th>編輯</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, key) in coupons" :key="key">
            <td>{{ item.title }}</td>
            <td>{{ item.code }}</td>
            <td>{{ item.percent }}%</td>
            <td>{{ new Date(item.due_date * 1000).toISOString().split('T')[0] }}</td>
            <td>
              <span v-if="item.is_enabled === 1" class="text-secondary">啟用</span>
              <span v-else class="text-muted">未啟用</span>
            </td>
            <td>
              <button class="btn btn-outline-secondary btn-sm" @click="openCouponModal(false, item)">
                <i class="fas fa-pen"></i>
              </button>
              <button class="btn btn-outline-danger btn-sm" @click="openDelCouponModal(item)">
                <i class="far fa-trash-alt"></i>
              </button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="modal fade" id="couponModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header bg-secondary text-white">
            <h5 class="modal-title" id="exampleModalLabel">優惠券</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label for="title">標題</label>
              <input type="text" class="form-control" id="title" v-model="tempCoupon.title"
                placeholder="請輸入標題">
            </div>
            <div class="form-group">
              <label for="coupon_code">優惠碼</label>
              <input type="text" class="form-control" id="coupon_code" v-model="tempCoupon.code"
                placeholder="請輸入優惠碼">
            </div>
            <div class="form-group">
              <label for="due_date">到期日</label>
              <input type="date" class="form-control" id="due_date"
                v-model="due_date">
            </div>
            <div class="form-group">
              <label for="price">折扣百分比</label>
              <input type="number" class="form-control" id="price"
                v-model="tempCoupon.percent" placeholder="請輸入折扣百分比">
            </div>
            <div class="form-group">
              <div class="form-check">
                <input class="form-check-input" type="checkbox"
                  :true-value="1"
                  :false-value="0"
                  v-model="tempCoupon.is_enabled" id="is_enabled">
                <label class="form-check-label" for="is_enabled">
                  是否啟用
                </label>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-danger" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-secondary" @click="updateCoupon">
              <i class="fas fa-spinner fa-spin"  v-if="loadingItem"></i> 確認
            </button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal fade" id="delCouponModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>刪除產品</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">是否刪除
            <strong class="text-danger">{{ tempCoupon.title }}</strong> 商品(刪除後將無法恢復)。
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-danger"@click="delCoupon">
              <i class="fas fa-spinner fa-spin"  v-if="loadingItem"></i> 確認刪除
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {mapGetters, mapActions} from 'vuex';
import $ from 'jquery';

export default {
  props: {
    config: Object,
  },
  data() {
    return {
      coupons: {},
      tempCoupon: {
        title: '',
        is_enabled: 0,
        percent: 100,
        due_date: 0,
        code: ''
      },
      due_date: new Date(),
      isNew: false
    };
  },
  computed: {
    ...mapGetters(['loadingItem'])
  },
  watch: {
    due_date() {
      const vm = this;
      const timestamp = Math.floor(new Date(vm.due_date) / 1000);
      vm.tempCoupon.due_date = timestamp;
    },
  },
  methods: {
    openCouponModal(isNew, item) {
      const vm = this;
      $('#couponModal').modal('show');
      vm.isNew = isNew;
      if (vm.isNew) {
        vm.tempCoupon = {};
      } else {
        vm.tempCoupon = Object.assign({}, item);
        const dateAndTime = new Date(vm.tempCoupon.due_date * 1000).toISOString().split('T');
        //console.log(dateAndTime);
        vm.due_date = dateAndTime[0];
      }
    },
    openDelCouponModal(item){
      let vm= this;
      $('#delCouponModal').modal('show');
      vm.tempCoupon= Object.assign({}, item);
    },
    getCoupons() {
      const vm = this;
      vm.$store.dispatch('updateLoading', true);
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupons`;
      this.$http.get(url, vm.tempProduct).then((response) => {
        //console.log(response);
        if(response.data.success){
          vm.coupons = response.data.coupons;
        }else{
          const message= response.data.message;
          const status= 'danger';
          vm.$store.dispatch('updateMessage', {message, status});
        }
        vm.$store.dispatch('updateLoading', false);
      });
    },
    updateCoupon() {
      const vm = this;
      vm.$store.dispatch('updateLoadingItem', true);
      if (vm.isNew) {
        const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`;
        this.$http.post(url, { data: vm.tempCoupon }).then((response) => {
          //console.log(response, vm.tempCoupon);
          vm.$store.dispatch('updateLoadingItem', false);
          $('#couponModal').modal('hide');
          const message= response.data.message;
          let status= 'secondary';
          if(!response.data.success){
            status= 'danger';
          };
          vm.$store.dispatch('updateMessage', {message, status});
          this.getCoupons();
        });
      } else {
        const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
        vm.due_date = new Date(vm.tempCoupon.due_date * 1000);
        this.$http.put(url, { data: vm.tempCoupon }).then((response) => {
          //console.log(response);
          vm.$store.dispatch('updateLoadingItem', false);
          $('#couponModal').modal('hide');
          const message= response.data.message;
          let status= 'secondary';
          if(!response.data.success){
            status= 'danger';
          };
          vm.$store.dispatch('updateMessage', {message, status});
          this.getCoupons();
        });
      }
    },
    delCoupon(){
      const vm= this;
      vm.$store.dispatch('updateLoadingItem', true);
      const api= `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
      this.$http.delete(api).then((response) => {
        //console.log(response, vm.tempCoupon);
        vm.$store.dispatch('updateLoadingItem', false);
        $('#delCouponModal').modal('hide');
        const message= response.data.message;
        const status= 'danger';
        vm.$store.dispatch('updateMessage', {message, status});
        vm.$store.dispatch('updateLoading', false);
        this.getCoupons();
      });
    },
  },
  created() {
    this.getCoupons();
  },
};
</script>


