<template>
  <div>
    <div class="text-right">
      <button class="btn btn-secondary" @click="showModal(true)">建立新產品</button>
    </div>
    <div class="col-12 my-5 overflow-auto">
      <table class="table">
        <thead>
          <tr>
            <th>產品名稱</th>
            <th>分類</th>
            <th>原價</th>
            <th>售價</th>
            <th>是否啟用</th>
            <th>編輯</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, key) in products" :key="item.id">
            <td>{{item.title}}</td>
            <td>{{item.category}}</td>
            <td>NT${{item.origin_price}}</td>
            <td>NT${{item.price}}</td>
            <td>
              <span v-if="item.is_enabled" class="text-secondary">啟用</span>
              <span v-else class="text-muted">未啟用</span>
            </td>
            <td>
              <button class="btn btn-outline-secondary btn-sm" @click="showModal(false, item)">
                <i class="fas fa-pen"></i>
              </button>
              <button class="btn btn-outline-danger btn-sm" @click="openDelProductModal(item)">
                <i class="far fa-trash-alt"></i>
              </button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <Pagination :pageData="pagination" @emitPages="getProducts"></Pagination>
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-secondary text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>產品</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image">輸入圖片網址</label>
                  <input type="text" class="form-control" id="image"
                    placeholder="請輸入圖片連結" v-model="tempProduct.imageUrl">
                </div>
                <div class="form-group">
                  <label for="customFile">或 上傳圖片
                    <i class="fas fa-spinner fa-spin" v-if="loadingItem== 'file'"></i>
                  </label>
                  <input type="file" id="customFile" class="form-control"
                    ref="files" @change="uploadFile">
                </div>
                <img img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                  class="img-fluid" :src="tempProduct.imageUrl" alt="">
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title">名稱</label>
                  <input type="text" class="form-control" id="title"
                    placeholder="請輸入標題" v-model="tempProduct.title">
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category">分類</label>
                    <input type="text" class="form-control" id="category"
                      placeholder="請輸入分類" v-model="tempProduct.category">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price">單位</label>
                    <input type="unit" class="form-control" id="unit"
                      placeholder="請輸入單位" v-model="tempProduct.unit">
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                  <label for="origin_price">原價</label>
                    <input type="number" class="form-control" id="origin_price"
                      placeholder="請輸入原價" v-model="tempProduct.origin_price">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price">售價</label>
                    <input type="number" class="form-control" id="price"
                      placeholder="請輸入售價" v-model="tempProduct.price">
                  </div>
                </div>
                <hr>

                <div class="form-group">
                  <label for="description">產品描述</label>
                  <textarea type="text" class="form-control" id="description"
                    placeholder="請輸入產品描述" v-model="tempProduct.description"></textarea>
                </div>
                <div class="form-group">
                  <label for="content">說明內容</label>
                  <textarea type="text" class="form-control" id="content"
                    placeholder="請輸入產品說明內容" v-model="tempProduct.content"></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input class="form-check-input" type="checkbox"
                      id="is_enabled" v-model="tempProduct.is_enabled" :true-value="1" :false-value="0">
                    <label class="form-check-label" for="is_enabled">
                      是否啟用
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-danger" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-secondary" @click="updateProduct">
              <i class="fas fa-spinner fa-spin" v-if="loadingItem"></i> 確認
            </button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>刪除產品</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">是否刪除
            <strong class="text-danger">{{ tempProduct.title }}</strong> 商品(刪除後將無法恢復)。
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-danger" @click="delProduct">
              <i class="fas fa-spinner fa-spin"  v-if="loadingItem"></i> 確認刪除
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {mapGetters, mapActions} from 'vuex';
import $ from 'jquery';
import Pagination from '../Pagination';

export default {
  data(){
    return{
      products: [],
      tempProduct: {},
      isNew: false,
      pagination: {}
    };
  },
  components: {
    Pagination
  },
  computed: {
    ...mapGetters(['loadingItem']),
  },
  methods: {
    getProducts(page= 1){
      const api= `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/products?page=${page}`;
      const vm= this;
      vm.$store.dispatch('updateLoading', true);
      this.$http.get(api).then((response) => {
        //console.log(response);
        if(response.data.success){
          vm.products= response.data.products;
          vm.pagination= response.data.pagination;
        }else{
          const message= response.data.message;
          const status= 'danger';
          vm.$store.dispatch('updateMessage', {message, status});
        }
        vm.$store.dispatch('updateLoading', false);
      });
    },
    showModal(isNew, item){
      if(isNew){
        this.tempProduct= {};
        this.isNew= true;
      }else{
        this.tempProduct= Object.assign({}, item);
        this.isNew= false;
      };
      $('#productModal').modal('show');
    },
    openDelProductModal(item){
      let vm= this;
      $('#delProductModal').modal('show');
      vm.tempProduct= Object.assign({}, item);
    },
    delProduct(){
      const vm= this;
      vm.$store.dispatch('updateLoadingItem', true);
      const api= `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
      this.$http.delete(api).then((response) => {
        //console.log(response, vm.tempProduct);
        vm.$store.dispatch('updateLoadingItem', false);
        $('#delProductModal').modal('hide');
        vm.$store.dispatch('updateLoading', false);
        const message= response.data.message;
        const status= 'danger';
        vm.$store.dispatch('updateMessage', {message, status});
        this.getProducts();
      });
    },
    updateProduct(){
      const vm= this;
      vm.$store.dispatch('updateLoadingItem', true);
      let api= `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`;
      let httpMethod= 'post';
      if(!this.isNew){
        api= `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
        httpMethod= 'put';
      }
      this.$http[httpMethod](api, {data: vm.tempProduct}).then((response) => {
        //console.log(response.data);
        vm.$store.dispatch('updateLoadingItem', false);
        if(response.data.success){
          $('#productModal').modal('hide');
          vm.getProducts();
          const message= response.data.message;
          const status= 'secondary';
          vm.$store.dispatch('updateMessage', {message, status});
        }else{
          $('#productModal').modal('hide');
          vm.getProducts();
        }
      });
    },
    uploadFile() {
      //console.log(this);
      const uploadedFile = this.$refs.files.files[0];
      const vm = this;
      const formData = new FormData();
      formData.append('file-to-upload', uploadedFile);
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`;
      vm.$store.dispatch('updateLoadingItem', 'file');
      this.$http.post(url, formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      }).then((response) => {
        //console.log(response.data);
        vm.$store.dispatch('updateLoadingItem', '');
        if (response.data.success) {
          // vm.tempProduct.imageUrl = response.data.imageUrl;
          // console.log(vm.tempProduct);
          vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl);
        }else{
          //this.$bus.$emit('message:push', response.data.message, 'danger');
          const message= response.data.message;
          const status= 'danger';
          vm.$store.dispatch('updateMessage', {message, status});
        }
      });
    },
  },
  created() {
    this.getProducts();
  }
}  
</script>



