import axios from 'axios';
import router from '../router';
import $ from 'jquery';
import store from '.';

export default{
  state: {
    cart: {
      carts: []
    },
    coupon_code: '',
  },
  actions: {
    getCart(context) {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      context.commit('LOADING', true);
      axios.get(url).then((response) => {
        if(response.data.success){
          context.commit('CART', response.data.data);
        }else{
          const message= response.data.message;
          const status= 'danger';
          store.dispatch('updateMessage', {message, status});
        };
        context.commit('LOADING', false);
      });
    },
    removeCartItem(context, id) {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      context.commit('LOADINGITEM', id);
      axios.delete(url).then((response) => {
        store.dispatch('getCart');
        context.commit('LOADINGITEM', '');
        const message= response.data.message;
        const status= 'danger';
        store.dispatch('updateMessage', {message, status});
      });
    },
    addCouponCode(context) {
      context.commit('LOADINGITEM', true);
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
      const coupon = {
        code: context.state.coupon_code
      };
      context.state.coupon_code= '';
      axios.post(url, { data: coupon }).then((response) => {
        //console.log(response);
        store.dispatch('getCart');
        context.commit('LOADINGITEM', false);
        let message= response.data.message;
        let status= 'danger';
        if(response.data.success){
          status= 'secondary';
          message= '已套用優惠券';
        };
        store.dispatch('updateMessage', {message, status});
      });
    }
  },
  mutations: {
    CART(state, payload){
      state.cart= payload;
    },
    COUPON_CODE(state, value){
      state.coupon_code= value;
    },
  },
  getters: {
    cart(state){
      return state.cart;
    },
    coupon_code(state){
      return state.coupon_code;
    },
  }
};


