import Vue from 'vue';
import Vuex from 'vuex';
import store from '.';
import axios from 'axios';
import $ from 'jquery';

import productModules from './product';
import cartModules from './cart';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    isLoading: false,
    loadingItem: '',
    messages: [],
  },
  actions: {
    updateLoading(context, status){
      context.commit('LOADING', status);
    },
    updateLoadingItem(context, status){
      context.commit('LOADINGITEM', status);
    },
    updateMessage(context, {message, status}) {
      const timestamp = Math.floor(new Date() / 1000);
      // this.messages.push({
      //   message,
      //   status,
      //   timestamp,
      // });
      context.commit('MESSAGEPUSH', {message, status, timestamp});
      //this.removeMessageWithTiming(timestamp);
      store.dispatch('removeMessageWithTiming', timestamp);
    },
    removeMessage(context, num) {
      //this.messages.splice(num, 1);
      context.commit('MESSAGESPLICE', num);
    },
    removeMessageWithTiming(context, timestamp) {
      const vm = this;
      setTimeout(() => {
        context.commit('MESSAGETIME', timestamp);
      }, 5000);
    },
  },
  mutations: {
    LOADING(state, status){
      state.isLoading= status;
    },
    LOADINGITEM(state, status){
      state.loadingItem = status;
    },
    MESSAGEPUSH(state, {message, status, timestamp}){
      state.messages.push({
        message,
        status,
        timestamp,
      });    
    },
    MESSAGESPLICE(state, num){
      state.messages.splice(num, 1);
    },
    MESSAGETIME(state, timestamp){
      state.messages.forEach((item, i) => {
        if (item.timestamp === timestamp) {
          state.messages.splice(i, 1);
        };
      });
    }
  },
  getters: {
    loadingItem(state){
      return state.loadingItem;
    },
    messages(state){
      return state.messages;
    }
  },
  modules: {
    productModules, cartModules
  } 
});


