<template>
  <div>
    <nav class="navbar navbar-expand-md navbar-light bg-light">
      <router-link to="/search" class="btn btn-outline-primary d-none d-md-block" type="button"><i class="fas fa-search"></i> 訂單查詢</router-link>
      <router-link to="/search" class="btn btn-outline-primary d-block d-md-none" type="button"><i class="fas fa-search"></i></router-link>
      <div class="navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav mr-auto ml-auto text-center">
          <li class="nav-item">
            <router-link to="/main" class="nav-link" href="#" active-class="active">首頁</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/products" class="nav-link" href="#" active-class="active">商品列表</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/cart" class="nav-link" href="#" active-class="active">
            購物車({{cart.carts.length}})
            </router-link>
          </li>
        </ul>
      </div>
      <router-link to="/admin/manage" class="btn btn-outline-primary d-none d-md-block" type="button"><i class="fas fa-cog"></i> 後台管理</router-link>
      <router-link to="/admin/manage" class="btn btn-outline-primary d-block d-md-none" type="button"><i class="fas fa-cog"></i></router-link>
    </nav>
  </div>
</template>
<script>
import {mapGetters, mapActions} from 'vuex';

export default{
  computed: {
    ...mapGetters(['cart'])
  },
  methods: {
    ...mapActions(['getCart']),    
  },
  created(){
    this.getCart();
  }
};
</script>




