<template>
  <div>
    <Loading :active.sync="isLoading"></Loading>
    <router-link to="/main">
      <Logo/>
    </router-link>
    <form class="form-signin" @submit.prevent="signin">
      <h5 class="strong mb-3 font-weight-normal">請登入管理員帳號</h5>
      <br>
      <label for="inputEmail" class="sr-only">Email address</label>
      <input type="email" id="inputEmail" class="form-control my-2" v-model="user.username" placeholder="Email address" required autofocus>
      <label for="inputPassword" class="sr-only">Password</label>
      <input type="password" id="inputPassword" class="form-control my-2" v-model="user.password" placeholder="Password" required>
      <div class="checkbox mb-3">
        <br>
      </div>
      <button class="btn btn-lg btn-primary btn-block" type="submit">
        <i class="fas fa-spinner fa-spin"  v-if="loadingItem"></i> 登入
      </button>
      <p class="mt-5 mb-3 text-muted">&copy; 2019 Copyright 僅於個人練習使用</p>
    </form>
    <Message/>
  </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex';
import Logo from '../Logo';
import Message from '../Message';

export default {
  name: 'HelloWorld',
  components: {
    Logo, Message
  },
  data () {
    return {
      user: {
        username: '',
        password: '',
      },
      isLoading: false,
    }
  },
  computed: {
    ...mapGetters(['loadingItem']),
  },
  methods: {
    signin(){
      const vm= this;
      vm.$store.dispatch('updateLoadingItem', true);
      const api= `${process.env.APIPATH}/admin/signin`;
      this.$http.post(api, vm.user).then((response) => {
        //console.log(response.data);
        if(response.data.success){
          vm.$store.dispatch('updateLoadingItem', false);
          vm.$router.push('/admin/manage');
          const message= response.data.message;
          const status= 'secondary';
          vm.$store.dispatch('updateMessage', {message, status});
        }else{
          vm.$store.dispatch('updateLoadingItem', false);
          const message= response.data.message;
          const status= 'danger';
          vm.$store.dispatch('updateMessage', {message, status});
        }
      });
      
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    html,
  body {
    height: 100%;
  }

  body {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    padding-top: 40px;
    padding-bottom: 40px;
    background-color: #f5f5f5;
  }

  .form-signin {
    width: 100%;
    max-width: 330px;
    padding: 15px;
    margin: auto;
  }
  .form-signin .checkbox {
    font-weight: 400;
  }
  .form-signin .form-control {
    position: relative;
    box-sizing: border-box;
    height: auto;
    padding: 10px;
    font-size: 16px;
  }
  .form-signin .form-control:focus {
    z-index: 2;
  }
  .form-signin input[type="email"] {
    margin-bottom: -1px;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }
  .form-signin input[type="password"] {
    margin-bottom: 10px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }
</style>



