<template>
  <div>
    <h5><b class="text-primary">購物清單</b></h5>
    <br>
    <ul class="list-group">
      <li class="list-group-item align-items-center d-flex justify-content-between py-1" v-for="item in cart.carts">
        <div class="align-middle d-none d-md-block col-md-3">
          <div style="height: 60px; background-size: 125%; background-position: center;"
            :style="{backgroundImage: `url(${item.product.imageUrl})`}">
          </div>
        </div>
        <div class="align-middle col-4 col-md-3">{{ item.product.title }}
          <div class="text-muted">{{ item.qty }}{{ item.product.unit }}</div>
        </div>
        <div class="align-middle col-6 col-md-4 text-muted">NT${{ item.final_total }}
          <div class="text-secondary" v-if="item.coupon">已套用優惠券</div>
        </div>
        <div class="col-2">
          <button type="button" class="btn btn-outline-danger btn-sm" @click="removeCartItem(item.id)">
            <i class="fas fa-spinner fa-spin" v-if="loadingItem=== item.id"></i><i class="far fa-trash-alt" v-else></i>
          </button>
        </div>
      </li>
      <li class="list-group-item align-items-center d-flex justify-content-between py-3 border-dark" v-if="cart.carts.length==0">
        <div class="col-12 text-center text-dark">目前無任何商品</div>
      </li>
      <li class="list-group-item align-items-center d-flex justify-content-between py-1" v-if="cart.final_total==cart.total">
        <span class="col-6 my-1"><b>總計</b></span>
        <span class="col-6 my-1 h5 text-right"><b>NT${{ cart.total }}</b></span>
      </li>
      <li class="list-group-item align-items-center d-flex justify-content-between py-1" v-if="cart.final_total!==cart.total">
        <span class="col-6 my-1"><del>總計</del></span>
        <span class="col-6 my-1 h5 text-right"><del>NT${{ cart.total }}</del></span>
      </li>
      <li class="list-group-item align-items-center d-flex justify-content-between py-1 border-secondary" v-if="cart.final_total!==cart.total">
          <span class="col-6 my-1 text-secondary"><b>折扣價 (-{{100-cart.carts[0].coupon.percent}}%)</b></span>
          <span class="col-6 my-1 text-secondary h5 text-right"><b>NT${{ cart.final_total }}</b></span>
      </li>
      <li class="list-group-item align-items-center d-flex justify-content-between py-1 border-secondary">
        <div class="input-group input-group-sm py-2">
          <input type="text" class="form-control border-secondary" v-model="coupon_code" placeholder="請輸入優惠碼">
          <div class="input-group-append">
            <button class="btn btn-outline-secondary" type="button" @click="addCouponCode">
              <i class="fas fa-spinner fa-spin" v-if="loadingItem==true"></i> 套用優惠碼
            </button>
          </div>
        </div>
      </li>
    </ul>
    <div class="text-right">
      <router-link to="/products">
        <button class="btn btn-primary">繼續購物</button>
      </router-link>
    </div>
  </div>
</template>
<script>
import {mapGetters, mapActions} from 'vuex';
import $ from 'jquery';

export default {
  data() {
    return {

    };
  },
  computed: {
    ...mapGetters(['loadingItem', 'cart', 'coupon_code']),
    coupon_code: {
      get(){
        return this.$store.state.coupon_code;   
      },
      set(value){
        this.$store.commit('COUPON_CODE', value);
      }
    },
  },
  methods: {
    ...mapActions(['getCart', 'addCouponCode']),
    
    removeCartItem(id) {
      this.$store.dispatch('removeCartItem', id);
    },
  },
  created() {
    this.getCart();
  },
};
</script>


