<template>
  <div>
    <form class="col-md-12 row justify-content-between py-4 my-4" @submit.prevent="payOrder">
      <div class="col-12 col-xl-5 justify-content-center px-4">
        <h5><b class="text-primary">購物清單</b></h5>
        <br>
        <table class="table">
          <thead>
            <th>品名</th>
            <th>數量</th>
            <th>單價</th>
          </thead>
          <tbody>
            <tr v-for="item in order.products" :key="item.id">
              <td class="align-middle">{{ item.product.title }}</td>
              <td class="align-middle">{{ item.qty }}{{ item.product.unit }}</td>
              <td class="align-middle">{{ item.final_total }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="2" class="text-right">總計</td>
              <td class="text-right">{{ order.total }}</td>
            </tr>
          </tfoot>
        </table>
      </div>
      <div class="col-12 col-xl-6 justify-content-center px-4">
        <h5><b class="text-primary">訂單資料</b></h5>
        <br>
        <table class="table">
          <tbody>
            <tr>
              <th>訂單編號</th>
              <td>{{ order.id }}</td>
            </tr>
            <tr>
              <th>姓名</th>
              <td>{{ order.user.name }}</td>
            </tr>
            <tr>
              <th>收件人電話</th>
              <td>{{ order.user.tel }}</td>
            </tr>
            <tr>
              <th>收件人地址</th>
              <td>{{ order.user.address }}</td>
            </tr>
            <tr>
              <th>Email</th>
              <td>{{ order.user.email }}</td>
            </tr>
            <tr>
              <th>付款狀態</th>
              <td>
                <span v-if="!order.is_paid">尚未付款</span>
                <span v-else class="text-secondary">付款完成 ({{ new Date(order.create_at * 1000).toISOString().split('T')[0]}})</span>
              </td>
            </tr>
          </tbody>
        </table>
        <!--<div class="text-right" v-if="order.is_paid === false">
          <button class="btn btn-outline-primary">
            <i class="fas fa-spinner fa-spin" v-if="loadingItem"></i> 確認付款去
          </button>
        </div>-->
        <div class="col-12" ref="paypal">
          <div class="text-danger small" v-if="order.is_paid === false">
            <p>Email: sb-2k36v1945453@personal.example.com</p>
            <p>Password: H-)U1Dqp</p>
          </div>
        </div>
      </div>
    </form>
    
  </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex';
import $ from 'jquery';

export default {
  data() {
    return {
      order: {
        user: {},
      },
      orderId: '',
      loaded: false,
      paypal: {
        total: 0,
        products: "Music Studio",
      }
    };
  },
  computed: {
    ...mapGetters(['loadingItem'])
  },
  methods: {
    getOrder() {
      const vm= this;
      const url= `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order/${vm.orderId}`;
      const src= `https://www.paypal.com/sdk/js?client-id=AYb6Vi9JAZf2OUmas3AU825PFgsbgZ4iNqQVDQBtC5X8IO0GXpIh9EYPeDmwY_1alFaeium9tbpaH_1p&currency=TWD`;
      vm.$store.dispatch('updateLoading', true);
      this.$http.get(url).then((response) => {
        if(response.data.success){
          vm.order = response.data.order;
          vm.paypal.total = response.data.order.total;
          if(response.data.order.is_paid === false){
            const script= document.createElement("script");
            script.src= src;
            script.addEventListener("load", vm.paypalLoaded);
            document.body.appendChild(script);
          }
        }else{
          const message= response.data.message;
          const status= 'danger';
          vm.$store.dispatch('updateMessage', {message, status});
        }
        //console.log(response);
        vm.$store.dispatch('updateLoading', false);
      });
    },
    payOrder() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/pay/${vm.orderId}`;
      vm.$store.dispatch('updateLoadingItem', true);
      this.$http.post(url).then((response) => {
        //console.log(response);
        if (response.data.success) {
          vm.getOrder();
          const message= response.data.message;
          const status= 'primary';
          vm.$store.dispatch('updateMessage', {message, status});
        }
        vm.$store.dispatch('updateLoadingItem', false);
      });
    },
    paypalLoaded() {
      const vm= this;
      this.loaded = true;
      window.paypal.Buttons({
        createOrder: (data, actions) => {
          return actions.order.create({
            purchase_units: [
              {
                description: this.paypal.products,
                amount: {
                  currency_code: "TWD",
                  value: this.paypal.total
                }
              }
            ]
          });
        },
        onApprove: async (data, actions) => {
          vm.$store.dispatch('updateLoading', true);
          const order = await actions.order.capture();
          vm.payOrder();
          vm.$refs.paypal.innerHTML="";
        },
        onError: err => {
          vm.$store.dispatch('updateLoading', true);
          //console.log(err);
          const message= err;
          const status= 'danger';
          vm.$store.dispatch('updateMessage', {message, status});
          vm.$store.dispatch('updateLoading', false);
        }
      })
      .render(this.$refs.paypal);
    }
  },
  created() {
    this.isLoading= true;
    this.orderId = this.$route.params.orderId;
    this.getOrder();
    //console.log(this.orderId);
  },
  mounted() {

  },
};
</script>


