<template>
  <div>
    <div class="row justify-content-between">
      <div class="justify-content-center col-10 col-md-4 col-xl-3">
        <Category/>
      </div>
      <div class="row my-5 col-12 col-md-8 col-xl-9">
        <div class="col-12 col-md-6 col-xl-4 mb-2" v-for="item in filterData" :key="item.id">
          <Card :cardData="item"></Card>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {mapGetters, mapActions} from 'vuex';
import $ from 'jquery';
import Card from '../CardComponent';
import Category from '../CategoryComponent';

export default {
  components: {
    Card, Category
  },
  data() {
    return {
     
    };
  },
  computed: {
    ...mapGetters(['products', 'searchText']),
    filterData() {
      const vm= this;
      const data= vm.products;
      if (vm.searchText) {
        return data.filter((item, index, array) => {
          return item.category=== vm.searchText; 
        });
      }else{
        return data;
      }
    },
  },
  methods: {
    getProducts() {
      this.$store.dispatch('getProducts');
    }
  },
  created() {
    this.getProducts();
  },
};
</script>


