<template>
  <div>
    <div class="list-group my-5 mr-4">
      <button type="button" class="list-group-item list-group-item-action" :class="{'active': searchText=== ''}"
      @click.prevent="changeSearchText('')">所有商品</button>
      <button type="button" class="list-group-item list-group-item-action" v-for="item in category" :class="{'active': item=== searchText}" 
      @click.prevent="changeSearchText(item)">{{item}}</button>
    </div>
  </div> 
</template>
<script>
import {mapGetters, mapActions} from 'vuex';
import $ from 'jquery';

export default {
  data() {
    return {
    };
  },
  computed: {
    ...mapGetters(['category', 'searchText']),
  },
  methods: {
    changeSearchText(value){
      this.$store.dispatch('changeSearchText', value);
    }
  },
}
</script>


