<template>
  <div>
    <nav class="navbar navbar-expand-md navbar-dark bg-dark">
      <router-link to="/main" class="btn btn-outline-secondary d-none d-md-block" type="button"><i class="fas fa-home"></i> 前台首頁</router-link>
      <router-link to="/main" class="btn btn-outline-secondary d-block d-md-none" type="button"><i class="fas fa-home"></i></router-link>
      <div class="navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav mr-auto ml-auto text-center">
          <li class="nav-item">
            <router-link to="/admin/manage" class="nav-link" href="#" active-class="active">產品列表</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/admin/coupons" class="nav-link" href="#" active-class="active">優惠券</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/admin/orders" class="nav-link" href="#" active-class="active">訂單列表</router-link>
          </li>
        </ul>
      </div>
      <button class="btn btn-outline-secondary d-none d-md-block" type="button" @click.prevent="signout"><i class="fas fa-user fa-1x"></i> 登出</button>
      <button class="btn btn-outline-secondary d-block d-md-none" type="button" @click.prevent="signout"><i class="fas fa-user fa-1x"></i></button>
    </nav>
  </div>
</template>
<script>
export default {
  name: 'BackNav',
  methods: {
    signout() {
      const vm = this;
      const url = `${process.env.APIPATH}/logout`;
      this.$http.post(url).then((response) => {
        //console.log(response.data);
        if (response.data.success) {
          const message= response.data.message;
          const status= 'secondary';
          vm.$store.dispatch('updateMessage', {message, status});
          vm.$router.push('/login');
        }
      });
    },
  },
};
</script>


