<template>
  <div>
    <div class="row justify-content-between">
      <div class="col justify-content-center col-12 col-md-5 col-xl-6 mb-4">
        <img :src="product.imageUrl" class="img-fluid" alt="">
      </div>
      <div class="col justify-content-center col-12 col-md-7 col-xl-6">
        <h4>
          <strong class="text-primary">{{ product.title }}</strong>
          <!--<router-link to="/products" class=""  @click.native="changeSearchText(product.category)">
            <span class="badge badge-secondary float-right">
            <small>{{ product.category }}</small>
            </span>
          </router-link>-->
        </h4>
        <br>
        <h5 class="text-right text-muted">{{ product.description }}</h5>
        <hr>
        <h5><strong>[產品介紹]</strong></h5>
        <br>
        <p class="text-muted">{{ product.content }}</p>
        <hr>
        <div class="d-flex justify-content-between align-items-baseline">
          <div class="h3" v-if="product.origin_price=== product.price">NT${{ product.origin_price }} 元</div>
          <del class="h5 text-muted" v-if="product.origin_price!== product.price">原價 NT${{ product.origin_price }} 元</del>
          <div class="h3 strong text-secondary" v-if="product.origin_price!== product.price">特價 NT${{ product.price }} 元</div>
        </div>
        <select name="" class="form-control my-3" v-model="num">
          <option :value="num" v-for="num in 10" :key="num">購買{{num}}{{product.unit}}</option>
        </select>
        <div class="text-right">
          <button type="button" class="btn btn-outline-danger" @click="addToCart(product.id, num)">
            <i class="fas fa-spinner fa-spin" v-if="loadingItem=== product.id"></i>
            加到購物車
          </button>
        </div>
      </div>
    </div>
    <div class="col-12">
        <h3 class="mb-5"><strong>其他商品</strong></h3>
        <Carousel :carouselId="carouselId[1]" :carouseHref="carouseHref[1]">
          <div class="row" slot="item1">
            <div class="d-none d-xl-block col-xl-3" v-for="item in 4">
              <Card :cardData="products[item - 1]"></Card>
            </div>
            <div class="d-none d-md-block d-xl-none col-md-4" v-for="item in 3">
              <Card :cardData="products[item - 1]"></Card>
            </div>
            <div class="d-block d-md-none col-12" v-for="item in 1">
              <Card :cardData="products[item - 1]"></Card>
            </div>
          </div>
          <div class="row" slot="item2">
            <div class="d-none d-xl-block col-xl-3" v-for="item in 4">
              <Card :cardData="products[item + 3]"></Card>
            </div>
            <div class="d-none d-md-block d-xl-none col-md-4" v-for="item in 3">
              <Card :cardData="products[item + 2]"></Card>
            </div>
            <div class="d-block d-md-none col-12" v-for="item in 1">
              <Card :cardData="products[item]"></Card>
            </div>
          </div>
          <div class="row" slot="item3">
            <div class="d-none d-xl-block col-xl-3" v-for="item in 4">
              <Card :cardData="products[item + 7]"></Card>
            </div>
            <div class="d-none d-md-block d-xl-none col-md-4" v-for="item in 3">
              <Card :cardData="products[item + 5]"></Card>
            </div>
            <div class="d-block d-md-none col-12" v-for="item in 1">
              <Card :cardData="products[item + 1]"></Card>
            </div>
          </div>
        </Carousel>
      </div>
  </div>
</template>
<script>
import {mapGetters, mapActions} from 'vuex';
import $ from 'jquery';
import Carousel from '../Carousel';
import Card from '../CardComponent';

export default {
  components: {
    Carousel, Card
  },
  data() {
    return {
      id: '',
      num: 1,
      carouselId: ['ads', 'products'],
      carouseHref: ['#ads', '#products']
    };
  },
  computed: {
    ...mapGetters(['product', 'loadingItem', 'products']),
  },
  methods: {
    addToCart(id, qty= 1) {
      this.$store.dispatch('addToCart', {id, qty});
    },
    changeSearchText(value){
      this.$store.dispatch('changeSearchText', value);
    },
    getProduct(id) {
      this.$store.dispatch('getProduct', id);
    },
    getProducts() {
      this.$store.dispatch('getProducts');
    }
  },
  created() {
    this.id = this.$route.params.id;
    this.getProduct(this.id);
    this.getProducts();
  },
};
</script>


